import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {UtilitybillpayProvider} from "../../providers/utilitybillpay/utilitybillpay";
import {AlertProvider} from "../../providers/alert/alert";

/**
 * Generated class for the Utilitybillpay01Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-utilitybillpay01',
  templateUrl: 'utilitybillpay01.html',
})
export class Utilitybillpay01Page {
  menuTitle;
  operationType;
  companyDetails;
  billingAccount;
  accountList = [];
  billCategories;
  billCategory;
  isNewReg;
  showNewReg;
  newAccount;
  debitAccDetails;
  utilityBillPayError01;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public tokenService: TokenServiceProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public billPayService: UtilitybillpayProvider,
              public alertProvider: AlertProvider) {

    this.utilityBillPayError01 = "";
    this.menuTitle = this.navParams.get("menuTitle");
    this.operationType = this.navParams.get("operationType");
    let company = this.navParams.get("companyDetails");
    this.companyDetails = JSON.parse(company);
    // console.log(this.companyDetails);

    let debitAccDetails = this.navParams.get("debitAccDetails");
    this.debitAccDetails = JSON.parse(debitAccDetails);

    this.billCategories = this.companyDetails.category;
    this.isNewReg = "0";
    this.showNewReg = false;
    this.newAccount = "";

    this.intCompanyWiseRegisteredAccount();
  }

  intCompanyWiseRegisteredAccount = function () {
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenComplete(data);
      });
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encCompanyCode = this.cryptoUtil.getEncryptVal(this.companyDetails.companyCode);
      this.billPayService.getRegisteredAccList(unMaskToken, appData.userId, appData.appId, encCompanyCode)
        .then(data => {
          this.onGetUtilityCompanyListComplete(data);
        });
    }
  };

  onGetUtilityCompanyListComplete = function (data) {
    this.utilityBillPayError01 = "";

    if (data.statusCode == 1) {
      this.accountList = data.object;
      // console.log(this.accountList);
    }
    else if (data.statusCode == 0) {
      this.utilityBillPayError01 = data.note;
      return;
    }

    else {
      this.utilityBillPayError01 = "System Error Contact with Administrator.";
      return;
    }
  };

  onSelectChange(selectedValue) {
    if (selectedValue == "Other") {
      this.isNewReg = "1";
      this.showNewReg = true;
    }
    else {
      this.isNewReg = "0";
      this.showNewReg = false;
    }
  }


  /////This functin return label value list for bill Payment


  fetchLabelValueForPayBill = function () {
    this.utilityBillPayError01 = "";

    if (this.billingAccount == "" || typeof(this.billingAccount) == "undefined") {
      this.utilityBillPayError01 = "Please select billing account.";
      return;
    }

    if (this.billingAccount == "Other") {
      if (this.newAccount == "" || typeof(this.newAccount) == "undefined") {
        this.utilityBillPayError01 = "Please Enter new account no.";
        return;
      }
      this.billingAccount = this.newAccount;
    }

    let length = this.billCategories.length;
    if (length > 0) {
      if (this.billCategory == "" || typeof(this.billCategory) == "undefined") {
        this.utilityBillPayError01 = "Please select bill category.";
        return;
      }
    }

    else {
      this.billCategory = "";
    }
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenCompleteForLabelVaue(data);
      });
  };

  onNewTokenGenCompleteForLabelVaue = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encCompanyCode = this.cryptoUtil.getEncryptVal(this.companyDetails.companyCode);
      let encCategory = this.cryptoUtil.getEncryptVal(this.billCategory);
      let encDebitAccount = this.cryptoUtil.getEncryptVal(this.debitAccDetails.accountNo);
      let encIsNewReg = this.cryptoUtil.getEncryptVal(this.isNewReg);
      let encBillingAccount = this.cryptoUtil.getEncryptVal(this.billingAccount);

      this.billPayService.getUtilityBillPayLabels(unMaskToken, appData.userId, appData.appId, encCompanyCode, encDebitAccount, encCategory, encIsNewReg, encBillingAccount)
        .then(data => {
          this.onGetUtilityBillLabelsComplete(data);
        });
    }
  };

  onGetUtilityBillLabelsComplete = function (data) {
    // console.log(data);
    if (data.statusCode == 1) {
      if (this.billCategory == "") {
        this.billCategoryDesc = "";
      }

      else {
        this.billCategoryDesc = this.getCategoryDesc();
      }

      this.navCtrl.push('Utilitybillpay02Page', {
        'labelValueList': JSON.stringify(data.object.labelsMap),
        'debitAccDetails': JSON.stringify(data.object.accInfoBean),
        'menuTitle': this.menuTitle,
        'selectedCategory': this.billCategory,
        'categoryDesc': this.billCategoryDesc,
        'companyDetails': JSON.stringify(this.companyDetails),
        'billingAccount': this.billingAccount,
        'isNewReg' : this.isNewReg
      });
    }

    else if (data.statusCode == 0) {
      this.utilityBillPayError01 = data.note;
      return;
    }

    else {
      this.utilityBillPayError01 = "System Error Contact with Administrator.";
      return;
    }
  };

  getCategoryDesc = function () {
    for (let i = 0; i < this.companyDetails.category.length; i++) {
      if (this.companyDetails.category[i].value == this.billCategory) {
        return this.companyDetails.category[i].label;
      }
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad Utilitybillpay01Page');
  }

}



